/* Szary- 58, 59, 61
Czerwony- 240, 50, 50
Szary2-71, 67, 67
Biały-250, 245, 245 */

.Footer{
    margin-left: 30px;
    margin-right: 30px;
    border-top: 2px solid rgb(240, 50, 50) ;
    border-left: 2px solid rgb(240, 50, 50) ;
    border-right: 2px solid rgb(240, 50, 50);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px ;

    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

p{
    color:rgb(250, 245, 245);
    font-weight: 600;
    font-size: 16px;
    margin: 0;
}

@media screen and (max-width: 1000px){
    .Footer{
        padding: 0;
        margin:0;
        display: flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }

}