.Main{
    margin: 30px ;
    display: grid;
    grid-template-columns:15% auto ;
    grid-template-rows:800px 160px;
}

.oferty{
background-color: red;
grid-row-start: 1;
grid-row-end:3;
}

.maincontent{
background-color: green;
grid-column-start: 2;
grid-column-end: 2;
grid-row-start: 1;
grid-row-end:3;


}

@media screen and (max-width: 1000px) { 
    .Main{
        margin: 0px;
        padding: 0px;
        grid-template-rows: 800px 200px;
    }
    .maincontent{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 1;
        grid-row-end:2;
    }
    .oferty{
        grid-column-start: 1;
        grid-column-end: 3 ;
        grid-row-start: 2;
        grid-row-end:2;
    }
}